          type: 'paragraph',
          body: `Budget ban đầu của mình chỉ có 100 đô thôi. Nhưng mà lúc mình phát hiện timex có đồng hồ tự động thì và cũng đẹp thì mình thích quá trời, muốn nhắn tin gửi bạn bên Mỹ mua giùm xách về luôn. Mình bắt đầu đeo đồng hồ từ lớp 6, đó giờ đeo 4 5 cái đồng hồ gì rồi, nhưng mà 3 năm nay không đeo nữa (vì không có đồng hồ để đeo). Mình vẫn tự nhủ là chiếc đồng hồ đầu tiên mình tự tay mua phải là đồng hồ tự động.`,
